.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.first-step-container button {
  width: 40px;
  height: 40px;
  margin: 20px;
}
.reading-lessions-selector button {
  width: 40px;
  height: 40px;
  margin: 10px;
}
.reading-lession-content {
  font-size: 60px;
}

.hidden{
  display: none !important;
}